import axios from "axios";
export async function menus() {
  return await axios({
    method: "GET",
    url: "https://13.124.69.255:90/api/menu/GetMenu_List",
    mode: "no-cors",
  }).catch((e) => {
    const { message } = e;
    return { error: true, message };
  });
}

export async function shipTypes() {
  return await axios({
    method: "GET",
    url: "https://13.124.69.255:90/api/menu/GetShipType_Combo",
    mode: "no-cors",
  }).catch((e) => {
    const { message } = e;
    return { error: true, message };
  });
}

export async function saveMenus(body) {
  return await axios({
    method: "POST",
    url: "https://13.124.69.255:90/api/menu/Save_Menu",
    mode: "no-cors",
    data: body,
  }).catch((e) => {
    console.log(e);
    const { message } = e;
    return { error: true, message };
  });
}
